import {useEffect, useRef, useState} from "react";
import {stopWatchStart, stopWatchPause, stopWatchReset} from "./stopWatchFunction";
import "../../../bootStrapStyle.css"

function StopWatchIndex(){

    const [timeText, setTimeText] = useState("00:00:00");
    const [startButtonText, setStartButtonText] = useState("Start");

    const [running, setRunning] = useState(false);
    const time = useRef(0);
    const interval = useRef(null);

    useEffect(() => {
        if(running){
            stopWatchStart(setStartButtonText, setTimeText, interval, time)
        } else{
            stopWatchPause(setStartButtonText, interval)
        }
    }, [running]);

    return <div className={"d-flex flex-column mb-5 align-items-center"}>
        <div className="fs-my">{timeText}</div>
        <div className="h-2">
            <div className={"d-flex"}></div>
            <button className={"btn btn-lg myButton"} onClick={() => setRunning(prevState => !prevState)} type={"button"}>{startButtonText}</button>
            <button className={"btn btn-lg myButton"} onClick={() => stopWatchReset(setRunning, setStartButtonText, setTimeText, interval, time)}>Reset</button>
        </div>
        </div>
}

export default StopWatchIndex