import axios from "axios";

export function getLoadMemoText(setText) {
    axios.get('/API/getMemo')
        .then(response => {
            setText(response.data.memo)
            console.log(response)
        })
        .catch(error => console.log(error))
}

export function postSaveMemoText(text) {
    axios.post('/API/postMemo', {
        memo: text
    }).then(response => {
        console.log(response)
    })
        .catch(error => console.log(error))
}