import {useEffect, useState} from "react";
import {getLoadMemoText, postSaveMemoText} from "./memoAPI";

function MemoIndex(){

    const [text, setText] = useState("");

    function memoTextAreaOnChange(event) {
        setText(event.target.value);
    }

    useEffect(() =>{
        getLoadMemoText(setText);
    }, [])

    return <div>
        <textarea value={text} onChange={memoTextAreaOnChange} className={"memoTextArea"}></textarea>
        <button onClick={() => {getLoadMemoText(setText)}}>Load</button>
        <button onClick={() => {postSaveMemoText(text)}}>Save</button>
    </div>
}

export default MemoIndex