import StopWatchIndex from "./stopWatch/StopWatchIndex";
import CalendarIndex from "./calendar/CalendarIndex";
import MemoIndex from "./memo/MemoIndex";
import TodoIndex from "./toDo/TodoIndex";

function SelectMode({mode}){

    if(mode === "stopWatch"){
        return <StopWatchIndex/>
    }
    if(mode === "calendar"){
        return <CalendarIndex/>
    }
    if(mode === "memo"){
        return <MemoIndex/>
    }
    if(mode === "todo"){
        return <TodoIndex/>
    }

}

export default SelectMode