export function stopWatchStart(setStartButtonText, setTimeText, interval, time) {
    setStartButtonText("Pause");
    interval.current = setInterval(() => {
        time.current += 1;

        let m = Math.floor(time.current/60);
        let h = Math.floor(m/60);
        let s = time.current%60;
        m = m%60;

        let th = m;
        let tm = h;
        let ts = s;
        if(th<10){
            th = "0" + h;
        }
        if(tm < 10){
            tm = "0" + m;
        }
        if(ts < 10){
            ts = "0" + s;
        }

        setTimeText(th+":"+tm+":"+ts);

    }, 1000);
}

export function stopWatchPause(setStartButtonText, interval){
    setStartButtonText("Start");
    clearInterval(interval.current);
}

export function stopWatchReset(setRunning, setStartButtonText, setTimeText, interval, time){
    clearInterval(interval.current);
    setRunning(false);
    setStartButtonText("Start");
    setTimeText("00:00:00");
    time.current = 0;
}