import Navbar from "./components/navigationBar/Navbar";
import Sidebar from "./components/sideBar/Sidebar"
import MainContent from "./components/mainContents/MainContent";
import {useState} from "react";
import EmptyMainContent from "./components/mainContents/EmptyMainContent";
import {
    BrowserRouter as Router,
    Routes,
    Route, useLocation
} from "react-router-dom";
import {TransitionGroup, CSSTransition} from "react-transition-group";
import "./transitionStyle.css"


function App() {

    const location = useLocation();

    return (
        <div className="App">
            <Navbar/>
            <Sidebar/>
            <TransitionGroup className={"transition-group"}>
                <CSSTransition key={location.pathname} classNames="pageSlider" timeout={500}>
                    <Routes location={location}>
                        <Route path={"/"} element={<EmptyMainContent/>}/>
                        <Route path={"/stopWatch"} element={<MainContent mode={"stopWatch"}/>}/>
                        <Route path={"/calendar"} element={<MainContent mode={"calendar"}/>}/>
                        <Route path={"/memo"} element={<MainContent mode={"memo"}/>}/>
                        <Route path={"/todo"} element={<MainContent mode={"todo"}/>}/>
                        <Route path={"/*"} element={<EmptyMainContent/>}/>
                    </Routes>
                </CSSTransition>
            </TransitionGroup>
        </div>
    );
}

export default App;
